import { createSlice } from '@reduxjs/toolkit';
import { RandomJokeModel } from '../../utils/models/JokeModel';

interface stateTypes {
  isLoading: boolean;
  jokes: RandomJokeModel[];
  savedJokes: RandomJokeModel[];
  errorMessage: string;
}

const initialState: stateTypes = {
  isLoading: false,
  jokes: [],
  savedJokes: [],
  errorMessage: '',
};

export const slice = createSlice({
  name: 'jokes',
  initialState,
  reducers: {
    // START LOADING
    startLoading(state) {
      state.isLoading = true;
    },

    // INITIALISE
    getInitialize(state) {
      state.isLoading = true;
      state.jokes = [];
      state.errorMessage = '';
    },

    // SET JOKES
    setJokes(state, action) {
      state.isLoading = false;
      state.jokes = action.payload.jokes;
    },

    // SET VIEWED JOKES
    addSavedJokes(state, action) {
      state.isLoading = false;
      state.savedJokes = action.payload.savedJokes;
    },

    // SET ERRORS
    setErrors(state, action) {
      state.isLoading = false;
      state.errorMessage = action.payload.errorMessage;
    },
  },
});

export const { actions } = slice;

export default slice.reducer;
