import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';

import useCategories from '../../redux/hooks/useCategories';
import DefaultLayout from '../../assets/layout/DefaultLayout';
import Categories from '../../components/Categories';
import homeActions from '../../redux/actions/home';
import request from '../../api/requests/home.request';
import { ResponseModel } from '../../utils/models/CategoryModel';
import Loading from '../../components/Loading';

const Home: React.FC = () => {
  const dispatch = useDispatch();
  const { categories } = useCategories();

  const [fetchStatus, setFetchStatus] = useState(false);

  useEffect(() => {
    const getCategories = async () => {
      if (categories.length === 0) {
        try {
          const result: ResponseModel = await request.getCategories();

          if (result.status) {
            setFetchStatus(true);
            dispatch(homeActions.setCategories(result.data));
          }
        } catch (error) {
          // eslint-disable-next-line no-console
          console.error(error);
        }
      } else {
        setFetchStatus(true);
      }
    }

    getCategories();
  }, [dispatch]);

  return (
    <DefaultLayout>
      {fetchStatus ? <Categories /> : <Loading />}
    </DefaultLayout>
  );
};

export default Home;
