import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { CustomCategoryModel } from '../../utils/models/CategoryModel';

type CategoryCardProps = {
  category: CustomCategoryModel;
}

const CategoryCard: React.FC<CategoryCardProps> = (props: CategoryCardProps) => {

  const { category } = props;
  const [hoverState, setHoverState] = useState(false);
  const [backgroundColor, setBackgroundColor] = useState('white');
  const [textColor, setTextColor] = useState('black');

  const onMouseEnter = () => {
    setHoverState(true);
  }

  const onMouseLeave = () => {
    setHoverState(false);
  }

  useEffect(() => {
    if (hoverState) {
      setBackgroundColor(category.color);
      setTextColor('white');
    } else {
      setBackgroundColor('white');
      setTextColor('black');
    }
  }, [hoverState]);

  return (
    <div className="card-category"
      role="button"
      tabIndex={category.id}
      onMouseEnter={onMouseEnter}
      onMouseLeave={onMouseLeave}
      style={{
        borderLeft: `7px solid ${category.color}`,
        backgroundColor: `${backgroundColor}`,
      }}>
        <Link to={`/jokes/${category.title}`}>
          <p className="card-category__title" style={{ color: `${textColor}` }}>
            <b>{category.title}</b>
          </p>
        </Link>
    </div>
  )
};

export default CategoryCard;
