import React, { useMemo } from 'react';
import CategoryCard from '../CategoryCard';

import useCategories from '../../redux/hooks/useCategories';

const Tabs: React.FC = () => {

  const { categories } = useCategories();

  const categoryCardMemo = useMemo(() => {
    const content: any[] = []

    categories.map((category) => {
      const temp = (
        <CategoryCard
          key={category.id}
          category={category}
        />
      );
      content.push(temp);
      return true;
    });
    return content
  }, [categories])

  return (
    <div className="categories">
      {categoryCardMemo}
    </div>
  )
};

export default Tabs;
