type routesProps = {
  path: string[] | string;
  exact: boolean;
  component: string;
}[];

const routes: routesProps = [
  {
    path: ["/", "/home"],
    exact: true,
    component: "Home",
  },
  {
    path: "/jokes/:category",
    exact: true,
    component: "Jokes",
  }
];


export default routes;
