import { AppDispatch, AppThunk } from '../store';
import { actions } from '../slices/home';
import { CustomCategoryModel } from '../../utils/models/CategoryModel';

const colors = ['#5A55AB', '#2FBBB0', '#347EB6'];

const homeActions = {
  resetState: (): AppThunk => async (dispatch: AppDispatch): Promise<void> => {
    dispatch(
      actions.getInitialize()
    );
  },
  setCategories:
    (categories: string[]): AppThunk =>
    async (dispatch: AppDispatch): Promise<void> => {
      const customs: CustomCategoryModel[] = [];
      const indexArray: number[] = [];

      while(indexArray.length < 3) {
        const randomInteger = Math.floor(Math.random() * categories.length) + 1;
        if(indexArray.indexOf(randomInteger) === -1) indexArray.push(randomInteger);
      }

      const results = categories.filter((category, index) =>
        indexArray.indexOf(index + 1) >= 0
      );

      results.forEach((category: string, index) => {
        const custom = {
          id: index,
          title: category,
          color: colors[index],
        }

        customs.push(custom);
      });

      dispatch(
        actions.setCategories({
          categories: customs
        })
      );
    }
};

export default homeActions;
