import React from 'react';
import { Provider } from 'react-redux';
import { BrowserRouter } from "react-router-dom";
import store from '../../redux/store';
import Home from '../Home';
import Router from '../../router';

const App: React.FC = () => (
  <BrowserRouter>
    <Provider store={store}>
      <Router>
        <Home />
      </Router>
    </Provider>
  </BrowserRouter>
);

export default App;
